import React, {Component} from 'react';
import Markdown from 'react-markdown';
import {Anchor, Row, Col} from 'antd';

const {Link} = Anchor;

class JsPage extends Component {
    constructor(props) {
        super(props);
    }


    render() {

        return (
            <div style={{background:"#c0d2bc"}}>
                <Row>
                    <Col span={19}>
                        <h3 id="git_reback"> Gitcommit </h3>
                        <Markdown source={"1\n" +
                        "\n" +
                        "    $ git reset --hard HEAD^         \n" +
                        "    $ git reset --hard HEAD~3        3n\n" +
                        "    $ git reset --hard commit_id     / commitsha\n" +
                        "\n" +
                        "2\n" +
                        "\n" +
                        "    git push origin HEAD --force"}/>

                        <h3 id="git_cover">  </h3>
                        <Markdown source={"   \n" +
                        "    git checkout develop_1  \n" +
                        "    git reset hard develop_2  \n" +
                        "    git push -f"}/>

                        <h3 id="git_cover">  </h3>
                        <Markdown source={"    git checkout dev_20220509\n" +
                        "    git branch |grep pre_001\n" +
                        "    git merge feature/20190302_pre_001\n" +
                        ","}/>

                        </Col>



                    <Col span={5}>
                        <Anchor showInkInFixed="true" style={{background:"#ebefe0"}}>
                            <Link href="#git_reback" title="Gitcommit"/>
                            <Link href="#git_cover" title=""/>
                            <Link href="#git_merge" title=""/>
                        </Anchor>
                    </Col>
                </Row>
            </div>

        );
    }
}

export default JsPage;


